/* src/css/style.css */

/* Styles généraux */
body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
  color: #333; /* Couleur de texte sombre */
  background-color: #ffffff; /* Fond blanc pour l'ensemble du site */
  overflow-x: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 20px;
}

/* Trait séparateur */
.separator {
  height: 2px; /* Épaisseur du trait */
  background-color: #00f2fe; /* Couleur du trait */
  margin: 40px auto; /* Espacement autour du trait */
  width: calc(100% - 40px); /* Largeur du trait avec marges */
}

/* Titres */
h1, h2 {
  margin-bottom: 30px;
  color: #00f2fe; /* Couleur des titres */
}

/* Navbar */
nav {
  background-color: rgba(255, 255, 255, 0.9); /* Fond blanc avec transparence */
  backdrop-filter: blur(10px);
  color: #333; /* Texte sombre pour le contraste */
  padding: 1rem;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

nav ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 20px;
}

nav ul li a {
 color:#00f2fe; /* Couleur des liens */
 text-decoration:none;
 transition: all .3s ease; 
 position:absolute; 
}

nav ul li a::after {
 content:'';
 position:absolute; 
 bottom:-5px; 
 left:0; 
 width:0; 
 height:2px; 
 background-color:#00f2fe; 
 transition: width .3s ease; 
}

nav ul li a:hover::after { 
 width :100%; 
}

/* Home Section */
#home {
 background-color:#ffffff; /* Fond blanc pour la section d'accueil */
 text-align:center ;
 padding :150px20px ; 
}

/* About Section */
#about {
 background-color:#ffffff; /* Fond blanc pour la section About */
}

.about-content {
 display:flex; 
 flex-direction :column; 
 align-items:center; 
}

.about-text {
 max-width :800px; /* Largeur maximale pour le texte */
 text-align:center; /* Centre le texte */
}

.about-image {
 position :relative ; 
 width :300px ; 
 height :300px ; 
 border-radius :50%; 
}

.about-image img {
 width :100%; 
 height :100%; 
 object-fit :cover ; 
}

/* Skills Section */
#skills {
 background-color:#f4f4f4; /* Couleur de fond pour la section Skills */
 padding :60px 0 ; /* Correction ici avec l'espace correct entre les valeurs */ 
}

/* Contact Section */
#contact { 
 background-color:#ffffff; /* Fond blanc pour la section Contact */ 
 padding :80px 0 ; /* Correction ici avec l'espace correct entre les valeurs */ 
}

/* Boutons avec animation d'ombre en noir */
button { 
 padding :15px ;  
 background-color:#00f2fe; /* Couleur des boutons */ 
 color:white ; /* Texte blanc pour les boutons */ 
 border:none ; 
 border-radius :5px ; 

/* Animation d'ombre au survol en noir */
transition :all .3s ease , box-shadow .3s ease ;

font-weight:bold ;  
} 

button:hover {  
box-shadow:
rgba(0,242,254,.4) -1px -1px,
rgba(0,242,254,.4) -1px -1px,
rgba(0,242,254,.4) -1px -1px;

transform : translateY(-2px); /* Légère élévation au survol */  
} 

/* Responsive Design */
@media (max-width:768px) { 

.about-image { width:200px;height:200;} 

.projects-grid { grid-template-columns:1fr;} 

nav ul { flex-direction:center; align-items:center;} 

nav ul li { margin-top:center;} 

}